import { useEffect, useState } from "react";
import Button from "./Button";

export default function Join({
    callerId, setCallerId,
    playerName, setPlayerName,
    onJoinGame
}) {
    const [playerNameError, setPlayerNameError] = useState(false);
    const [callerIdError, setCallerIdError] = useState(false);

    useEffect(() => {
        setCallerIdError(callerId == '');
        setPlayerNameError(playerName == '');
    }, [callerId, playerName]);

    const clickJoinGame = () => {
        if (callerIdError, playerNameError) return;

        onJoinGame();
    };

    return (
        <div className="join-game">
            <h3 className="mb-4 text-center border-top pt-2">Join Game</h3>
            <div className="row">
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="caller-id">Caller ID</label>
                        <input type="text" className="form-control" id="caller-id" value={callerId} onChange={(e) => setCallerId(e.currentTarget.value)} placeholder="Caller ID" />
                        {
                            callerIdError
                            &&
                            <span className="text-danger">Enter Caller ID</span>
                        }
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="your-name">Your Name</label>
                        <input type="text" className="form-control" id="your-name" value={playerName} onChange={(e) => setPlayerName(e.currentTarget.value)} placeholder="Your Name" />
                        {
                            playerNameError
                            &&
                            <span className="text-danger">Enter Your Name</span>
                        }
                    </div>
                </div>
            </div>
            <div className="text-center">
                <Button buttonClass="primary" onButtonClick={clickJoinGame}>Join</Button>
            </div>
        </div>
    );
}