import Button from "./Button";

export default function Init({
    onCreateGame,
    onJoinGame
}) {

    return (
        <div className="d-flex justify-content-center gap-2">
            <Button buttonClass="primary" onButtonClick={onCreateGame}>Create Game</Button>
            <Button buttonClass="success" onButtonClick={onJoinGame}>Join Game</Button>
        </div>
    );
}