import { useEffect, useState } from 'react';
import './App.css';
import Create from './Create';
import Init from './Init';
import Join from './Join';
import Dashboard from './Dashboard';
import Peer from 'peerjs';
import GameBoard from './GameBoard';

const generateChars = () => {
  let start = 65;
  let _chars = [];
  for (let i = 0; i < 26; i++) {
    _chars.push(String.fromCharCode(start + i));
  }
  return _chars;
};
const chars = generateChars();

const peer = new Peer(Math.round(Math.random(10) * 10) + '');

function App() {
  const minRows = 3;
  const minCols = 3;
  const minMatchCase = 3;
  const minPlayers = 2;
  const maxPlayers = 5;

  const [isInitGame, setIsInitGame] = useState(true);
  const [isCreateGame, setIsCreateGame] = useState(false);
  const [isJoinGame, setIsJoinGame] = useState(false);
  const [isDashboard, setIsDashboard] = useState(false);
  const [isGameBoard, setIsGameBoard] = useState(false);
  const [isCreator, setIsCreator] = useState(false);
  const [tern, setTern] = useState();

  const [rows, setRows] = useState(minRows);
  const [cols, setCols] = useState(minCols);
  const [matchCase, setMatchCase] = useState(minMatchCase);
  const [playersCount, setPlayersCount] = useState(minPlayers);

  const [playerName, setPlayerName] = useState('');
  const [playerChar, setPlayerChar] = useState();
  const [callerId, setCallerId] = useState('');

  const [players, setPlayers] = useState([]);
  const [currentPlayers, setCurrentPlayers] = useState(0);
  const [connect, setConnect] = useState(null);
  
  const [gameHistory, setGameHistory] = useState({});
  const [gameWinner, setGameWinner] = useState(null);

  const sendData = (data) => {
    if (!connect) return;

    connect.send(data);
  };

  useEffect(() => {
    if (!connect) return;

    if (isCreator) {
      sendData({
        players: players,
        rows: rows,
        cols: cols,
        matchCase: matchCase
      });
    }
  }, [currentPlayers]);

  useEffect(() => {
    if (players.length == 0) return;

    setTern(0);
    setPlayerChar(() => players.filter((player) => player.name == playerName)[0].char);
  }, [players]);

  const nextTern = () => {
    setTern(() => tern == playersCount - 1 ? 0 : tern + 1);
  };

  useEffect(() => {
    sendData({ tern: tern });
  }, [tern]);

  useEffect(() => {
    peer.on("connection", (conn) => {
      setConnect(conn);
    });
  }, []);

  const hideAll = (callback) => {
    setIsInitGame(false);
    setIsCreateGame(false);
    setIsJoinGame(false);
    setIsDashboard(false);
    setIsGameBoard(false);

    callback(true);
  };

  const clickCreateGame = () => {
    hideAll(setIsCreateGame);
  };

  const clickJoinGame = () => {
    hideAll(setIsJoinGame);
  };

  const createGame = () => {
    hideAll(setIsDashboard);
    setCallerId(peer.id);
    setIsCreator(true);
    joinPlayer(playerName);
    setCurrentPlayers(currentPlayers + 1);
  };

  const joinGame = () => {
    setConnect(() => peer.connect(callerId));
  };

  const startGame = () => {
    hideAll(setIsGameBoard);

    if (isCreator) {
      sendData({ startGame: true });
    }
  };

  useEffect(() => {
    if (!connect) return;

    connect.on("open", () => {
      if (!isCreator) {
        sendData({ player: playerName });
      }
    });

    connect.on("data", (data) => {
      if (data.player) {
        joinPlayer(data.player);
        setCurrentPlayers(currentPlayers + 1);
      }

      if (data.players) {
        setPlayers(data.players);
        setRows(data.rows);
        setCols(data.cols);
        setMatchCase(data.matchCase);
        hideAll(setIsDashboard);
      }

      ('startGame' in data) && startGame();
      ('tern' in data) && setTern(data.tern);
      ('history' in data) && setGameHistory(data.history);
      ('winner' in data) && setGameWinner(data.winner);
    });
  }, [connect]);

  const joinPlayer = (name) => {
    players.push({
      name: name,
      char: chars[players.length],
    });
    setPlayers([...players]);
  };

  return (
    <div>
      <h1 className="mb-4 text-center">Tic Tac Toe</h1>

      {isInitGame && <Init onCreateGame={clickCreateGame} onJoinGame={clickJoinGame} />}

      {
        isCreateGame &&
        <Create
          rows={rows} setRows={setRows} minRows={minRows}
          cols={cols} setCols={setCols} minCols={minCols}
          matchCase={matchCase} setMatchCase={setMatchCase} minMatchCase={minMatchCase}
          playersCount={playersCount} setPlayersCount={setPlayersCount} minPlayers={minPlayers} maxPlayers={maxPlayers}
          playerName={playerName} setPlayerName={setPlayerName}
          onCreateGame={createGame}
        />
      }

      {
        isJoinGame &&
        <Join
          callerId={callerId} setCallerId={setCallerId}
          playerName={playerName} setPlayerName={setPlayerName}
          onJoinGame={joinGame}
        />
      }

      {
        isDashboard &&
        <Dashboard
          players={players}
          isCreator={isCreator}
          onStartGame={startGame}
          peerId={callerId}
        />
      }

      {
        isGameBoard &&
        <GameBoard
          players={players}
          playerChar={playerChar}
          tern={tern}
          nextTern={nextTern}
          rows={rows}
          cols={cols}
          matchCase={matchCase}
          sendData={sendData}
          gameHistory={gameHistory}
          gameWinner={gameWinner}
        />
      }
    </div>
  );
}

export default App;
