import { useEffect, useState } from "react";
import Button from "./Button";
import './Game.css';

export default function Game({
    players,
    playerChar,
    rows,
    cols,
    matchCase,
    tern, nextTern,
    sendData,
    gameHistory,
    gameWinner
}) {
    const rowsData = Array.from({ length: rows }).fill(null);
    const colsData = Array.from({ length: cols }).fill(null);

    const initHistory = () => {
        var history = {};
        rowsData.map((rowNum, rowIndex) => {
            colsData.map((colNum, colIndex) => {
                history[`${rowIndex}-${colIndex}`] = null;
            });
        });
        return history;
    }

    const initHistoryClasses = () => {
        var historyClasses = {};
        for (var index in history) {
            historyClasses[index] = 'box-' + index;
        }
        return historyClasses;
    };

    const [history, setHistory] = useState(initHistory);
    const [historyClasses, setHistoryClasses] = useState(initHistoryClasses);
    const [winner, setWinner] = useState('');

    useEffect(() => {
        Object.keys(gameHistory).length > 0 && setHistory(gameHistory);
    }, [gameHistory]);

    const generateWinningCombinations = () => {
        const winningCombinations = [];

        // Horizontal Wins (rows)
        for (let row = 0; row < rows; row++) {
            for (let col = 0; col <= cols - matchCase; col++) {
                const combination = [];
                for (let i = 0; i < matchCase; i++) {
                    combination.push(`${row}-${col + i}`);
                }
                winningCombinations.push(combination);
            }
        }

        // Vertical Wins (columns)
        for (let col = 0; col < cols; col++) {
            for (let row = 0; row <= rows - matchCase; row++) {
                const combination = [];
                for (let i = 0; i < matchCase; i++) {
                    combination.push(`${row + i}-${col}`);
                }
                winningCombinations.push(combination);
            }
        }

        // Diagonal Wins (top-left to bottom-right)
        for (let row = 0; row <= rows - matchCase; row++) {
            for (let col = 0; col <= cols - matchCase; col++) {
                const combination = [];
                for (let i = 0; i < matchCase; i++) {
                    combination.push(`${row + i}-${col + i}`);
                }
                winningCombinations.push(combination);
            }
        }

        // Diagonal Wins (top-right to bottom-left)
        for (let row = 0; row <= rows - matchCase; row++) {
            for (let col = matchCase - 1; col < cols; col++) {
                const combination = [];
                for (let i = 0; i < matchCase; i++) {
                    combination.push(`${row + i}-${col - i}`);
                }
                winningCombinations.push(combination);
            }
        }

        return winningCombinations;
    };

    const winningCombinations = generateWinningCombinations();

    const checkWinner = () => {
        var isWinner = false;
        winningCombinations.map((strike) => {
            let matches = 0;
            strike.map((index) => {
                if (gameHistory[index] && gameHistory[index] == players[tern].char) {
                    matches++;
                }
            });

            if (matches == matchCase) {
                setWinner(players[tern].char);
                const newHistoryClasses = historyClasses;
                strike.map((index) => {
                    newHistoryClasses[index] = 'matched ' + newHistoryClasses[index];
                });
                setHistoryClasses(newHistoryClasses);
                isWinner = true;
            }
        });

        if (!isWinner) {
            nextTern();
        }
    };

    useEffect(() => {
        gameWinner && checkWinner();
    }, [gameWinner]);

    useEffect(() => {
        sendData({ winner: winner });
    }, [winner]);

    const handleClick = (rowIndex, colIndex) => {
        if (players[tern].char != playerChar) return;
        if (winner) return;
        if (history[`${rowIndex}-${colIndex}`]) return;

        const currentHistory = history;
        currentHistory[`${rowIndex}-${colIndex}`] = players[tern].char;
        setHistory(currentHistory);
        sendData({ history: currentHistory });
        checkWinner();
    };

    return (
        <div className="mt-2">
            {rowsData.map((rowNum, rowIndex) => {
                return (
                    <div key={rowIndex} className="text-center">
                        {colsData.map((colNum, colIndex) => {
                            let index = `${rowIndex}-${colIndex}`;
                            return <Button extraClasses={historyClasses[index] + " box"} key={index} onButtonClick={() => handleClick(rowIndex, colIndex)}>{history[index]}</Button>;
                        })}
                    </div>
                );
            })}
        </div>
    );
}