import { useEffect, useState } from "react";
import Button from "./Button";

export default function Create({
    rows, setRows, minRows,
    cols, setCols, minCols,
    matchCase, setMatchCase, minMatchCase,
    playersCount, setPlayersCount, minPlayers, maxPlayers,
    playerName, setPlayerName,
    onCreateGame
}) {


    const [rowsError, setRowsError] = useState(false);
    const [colsError, setColsError] = useState(false);
    const [matchCaseError, setMatchCaseError] = useState(false);
    const [playersCountError, setPlayesCountError] = useState(false);
    const [playerNameError, setPlayerNameError] = useState(false);

    useEffect(() => {
        setRowsError(rows < minRows);
        setColsError(cols < minCols);
        setMatchCaseError(!(matchCase <= rows || matchCase <= cols) || matchCase < minMatchCase);
        setPlayesCountError(playersCount < minPlayers || playersCount > maxPlayers);
        setPlayerNameError(playerName == '');
    }, [rows, cols, matchCase, playersCount, playerName]);

    const clickCreateGame = () => {
        if (rowsError || colsError || matchCaseError || playersCountError) return;
        if (playerNameError) return;

        onCreateGame();
    };

    return (
        <div className="create-game">
            <h3 className="mb-4 text-center border-top pt-2">Create Game</h3>
            <div className="row">
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="rows">Rows</label>
                        <input type="number" className="form-control" id="rows" value={rows} onChange={(e) => setRows(e.currentTarget.value)} placeholder="Rows" />
                        {
                            rowsError
                            &&
                            <span className="text-danger">Minimum {minRows} Rows</span>
                        }
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="columns">Columns</label>
                        <input type="number" className="form-control" id="columns" value={cols} onChange={(e) => setCols(e.currentTarget.value)} placeholder="Columns" />
                        {
                            colsError
                            &&
                            <span className="text-danger">Minimum {minCols} Columns</span>
                        }
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="win-condition">Winning Condition</label>
                        <input type="number" className="form-control" id="win-condition" value={matchCase} onChange={(e) => setMatchCase(e.currentTarget.value)} placeholder="Winning Condition" />
                        {
                            matchCaseError
                            &&
                            <span className="text-danger">Should match with Rows or Columns</span>
                        }
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-2">
                        <label htmlFor="max-players">Max Players</label>
                        <input type="number" className="form-control" id="max-players" value={playersCount} onChange={(e) => setPlayersCount(e.currentTarget.value)} placeholder="Maximum Players" />
                        {
                            playersCountError
                            &&
                            <span className="text-danger">Players Between {minPlayers} to {maxPlayers}</span>
                        }
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-12">
                    <div className="form-group mb-2">
                        <label htmlFor="your-name">Your Name</label>
                        <input type="text" className="form-control" id="your-name" value={playerName} onChange={(e) => setPlayerName(e.currentTarget.value)} placeholder="Your Name" />
                        {
                            playerNameError
                            &&
                            <span className="text-danger">Enter Your Name</span>
                        }
                    </div>
                </div>
            </div>
            <div className="text-center">
                <Button buttonClass="primary" onButtonClick={clickCreateGame}>Create</Button>
            </div>
        </div>
    );
}