export default function Button({
    children,
    buttonClass,
    extraClasses,
    onButtonClick
}) {
    buttonClass = buttonClass || 'light';

    return (
        <button
            type="button"
            className={'btn btn-' + buttonClass + ' border ' + extraClasses}
            onClick={onButtonClick}
        >
            {children}
        </button>
    );
}