import Game from "./Game";
import Table from "./Table";

export default function GameBoard({
    players,
    playerChar,
    tern, nextTern,
    rows,
    cols,
    matchCase,
    sendData,
    gameHistory,
    gameWinner
}) {
    return (
        <div className="game-board">
            <div className="row">
                <div className="col-md-6">
                    <Table players={players} tern={tern} />
                </div>
                <div className="col-md-6">
                    <Game
                        players={players}
                        playerChar={playerChar}
                        rows={rows}
                        cols={cols}
                        matchCase={matchCase}
                        tern={tern}
                        nextTern={nextTern}
                        sendData={sendData}
                        gameHistory={gameHistory}
                        gameWinner={gameWinner}
                    />
                </div>
            </div>
        </div>
    );
}