import Button from "./Button";
import Table from "./Table";

export default function Dashboard({
    players,
    isCreator,
    peerId,
    onStartGame
}) {
    return (
        <div className="dashboard">
            <h3 className="mb-4 text-center border-top pt-2">Players on Room ({peerId})</h3>

            <Table players={players} />

            {
                isCreator &&
                <div className="text-center">
                    <Button buttonClass="primary" onButtonClick={onStartGame}>Start Game</Button>
                </div>
            }
        </div>
    );
}